<script>
import Panel from './components/Panel'
import TOPNList from './components/TOPNList'
import PieChart from './components/PieChart.vue'
import BarChart from './components/BarChart.vue'
import LineChart from './components/LineChart'
import DiskCapacity from './components/DiskCapacity'
import HardWareChart from './components/HardWareChart'
import ServerStatusChart from './components/ServerStatusChart'
import AutoScroll from './../plugins/AutoScroll'
var mockData = require('./mock-data');
// import mockData from './mock-data'
export default {
  name: 'Home',
  components: {
    Panel,
    TOPNList,
    PieChart,
    BarChart,
    LineChart,
    HardWareChart,
    DiskCapacity,
    ServerStatusChart
  },
  data() {
    return {
      os: null,
      computer: null,
      antivirus: null,
      healthData: [],
      usbData: [],
      srvData: {},
      diskData: null,
      topn: null,
      topnData: null,
      online: {},
      repair: null
    }
  },
  mounted() {
    console.log(mockData);
    const { os, computer, antivirus, healthData, usbData, srv, disk, topn, topnData, online, repair } = mockData;
    this.os = os;
    this.computer = computer;
    this.antivirus = antivirus;
    this.healthData = healthData;
    this.usbData = usbData;
    this.srvData = srv;
    this.diskData = disk;
    this.topn = topn;
    this.topnData = topnData;
    this.online = online;
    this.repair = repair;

    const bodyWrapper = this.$refs.healthTable.$refs.bodyWrapper;
    bodyWrapper.parentNode.removeChild(bodyWrapper);

    setInterval(() => {
      this.usbData.push(this.usbData[0]);
      this.usbData.splice(0, 1);
    }, 3000);
  }
}
</script>
