<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "BarChart",
  props: {
    data: { type: Array, default: () => { } }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    data(val) {
      if (this.chart) {
        this.chart.setOption(this.getOptions(val));
      }
    }
  },
  methods: {
    getOptions() {
      // 指定图表的配置项和数据
      var option = {
        xAxis: {
          show: false,
          type: 'value'
        },
        yAxis: {
          show: true,
          type: 'category',
          axisLabel: {
            formatter: '{value}',
            textStyle: {
              fontSize: '14',
              color: '#fff'
            }
          },
          z: 10,
          data: []
        },
        grid: {
          top: 0,
          left: 30,
          right: 0,
          bottom: 10,
          containLabel: true,
        },
        series: [{
          data: [],
          type: 'bar',
          barWidth: 40,
          itemStyle: {
            normal: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                offset: 0,
                color: 'rgba(0,223,227,1)' // 0% 处的颜色
              }, {
                offset: 1,
                color: 'rgba(51,139,238,1)' // 100% 处的颜色
              }], false),
            }
          },
          label: {
            fontSize: 16,
            formatter: '{c}台',
            show: true,
            position: 'insideRight',
            textBorderColor: '#fff',
            textBorderWidth: 1
          }
        }]
      };

      this.data && this.data.forEach(item => {
        option.yAxis.data.unshift(item.name);
        option.series[0].data.unshift(item.value);
      });

      return option;
    }
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.chart);
    this.chart.setOption(this.getOptions());
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 500px;
  height: 280px;
}
</style>
